

:root{

  --color-primary:        #002c37;
  --color-grey-light-1:   #FAFBFB;
  --color-grey-light-2:   #DADEE4;

  --color-grey-dark-1:    #AEBAC3;
  --color-grey-dark-2:    #8998A5;
  --color-grey-dark-3:    #475560;

  --font-primary: 'Open Sans', sans-serif;
  --font-title: 'Times New Roman', Times, serif;

  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, .3);

}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html{
  box-sizing: border-box;
  font-size: 62.5%; /* So that 1rem = 10px (10px/16px (browsers default) = 62.5% -> 1rem = 10px */
}

body{
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font-primary);
  font-weight: 300;
  color: #002c37;
  color: var(--color-primary); 
  background-color: #FAFBFB; 
  background-color: var(--color-grey-light-1);
}

.App {
  overflow-x: hidden;
  overflow-y: visible;

  display: grid;
  grid-template-rows: [header-start] 6rem  [header-end full-start] calc(100vh - 6rem) [full-end]; 
  grid-template-columns: 35vw 65vw;
}

@media only screen and (max-width: 75em){
  html{
      font-size: 55%;
  }
}

@media only screen and (max-width: 470px){
  html{
    font-size: 40%;
  }
}

.header{
    color: var(--color-grey-dark-2);
    background-color: white;
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: .3rem;
    grid-column: 1 / -1;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__logo{
    height: 2.5rem;
}

.header__search{
    display: flex;
    align-items: center;

    position: relative;
    margin-right: 1.5rem;
    height: 100px; /* bigger than your svg */
    width: 100px; /* bigger than your svg */
}

.magnifying-icon{
    margin-left: 1.5rem;
}

.nav-btn{
    border: none;
    border-radius: 0;

    background-color: var(--color-grey-dark-2);
    height: .2rem;
    width: 2rem;
    margin-right: 1.5rem;
    margin-left: 1.5rem;
}

.nav-btn::before{
    background-color: var(--color-grey-dark-2);
    height: .2rem;
    width: 2rem;
    content: "";
    display: block;
}

.nav-btn::before {-webkit-transform: translateY(-.5rem);transform: translateY(-.5rem);}

.main-content{
    font-family: 'Times New Roman', Times, serif;
    color: var(--color-primary);
    
    line-height: .95;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    -webkit-transform: translateX(40%);

            transform: translateX(40%);
    z-index: 1;
}

.main-content__title{
    position: relative;
    font-size: 12rem;
    letter-spacing: .2rem;
}

.main-content__subtitle{
    position: relative;
    font-size: 1.5rem;
    text-transform: uppercase;
    letter-spacing: .2rem;
    margin-top: 2rem;
}

.main-content__button{
    position: relative;
    font-size: 1.5rem;
    text-transform: uppercase;
    letter-spacing: .2rem;
    padding: 2rem;
    background-color: inherit;
    color: inherit;
    border-color: var(--color-primary);
    margin-top: 4rem;
}

#sierra{
    margin-bottom: .1rem;
}

#nevada{
    margin-top: .1rem;
}

/* ---- BUTTON HOVER ANIMATION ---- */

.main-content__button::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 3px;
    background-color: var(--color-primary);
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    z-index: -1;
    transition: width .3s cubic-bezier(0,0,0,1);
}

.main-content__button:hover::before{
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    width: 100%;
}

.main-content__button:hover{
    color: white;
    cursor: pointer;
}

/* ---- BOX COVERING TEXT ANIMATION ---- */

/* The blue box begins from position left 100% with 0 width */
.main-content__title::after{
    content: "";
    position: absolute;
    top: 0;
    left: 100%;
    height: 100%;
    width: 0px;
    background-color: var(--color-primary);
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    transition: left  .3s  cubic-bezier(0,0,0,1),
                width  .3s  cubic-bezier(0,0,0,1);
                
}

/*  After the button is pressed, the text takes the class animated which changes
    the width to 100% and scales it on the Y direction 100% */
.animated1BoxCoverText::after{
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    width: 100%;
    left: 0;
}


/* ---- BOX ERASING TEXT ANIMATION ---- */

/* The width of the box becomes 0 */
.animated2BoxEraseText::after{
    width: 0;
}

/* The text behind the box becomes transparent */
.animated2BoxEraseText{
    color: transparent;
    visibility: hidden;
    transition: visibility .2s ease-in-out;
}
.main-video{
    position: relative;
    overflow: hidden;
    z-index: -1;
}

.main-video__cloudsimg{
    position: absolute;
    width: 100%;
    top: 100%;
}

.main-video__text{
    position: absolute;
    font-size: 5rem;
    font-family: 'Times New Roman', Times, serif;
    color: var(--color-primary);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;

    /* Center Positioning of text*/
    text-align: center;
    width: 100%;
    top: 55vh;
    /*visibility: hidden;*/
}

.main-video__title{
    font-size: 5rem;
    visibility: hidden;
}

.main-video__subtitle{
    font-size: .2em;
    text-transform: uppercase;
    letter-spacing: .2rem;
    visibility: hidden;
}

.main-video__content{
    position: absolute;
    margin: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: auto;
    width: 110%;
    object-fit: cover;
}

/* ----  GRID ---- */
.main-video__grid{
    /* Position the Grid exactly like the video */
    position: absolute;
    margin: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: auto;
    width: 100%;
    object-fit: cover; 
    
    margin-top: 350px;
    padding: 15rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 5rem;
}

.main-video__grid-shape{
    justify-self: center;
    overflow: hidden;
    width: 16.5rem;
    height: 25rem;
}
.main-video__grid-image{
    display: block;
    height: 100%;
    opacity: 0;
    -webkit-transform: translateX(-1.5rem) scale(2);
            transform: translateX(-1.5rem) scale(2);
}

.main-video__grid-text{
    padding-left: 4rem;
    padding-right: 4rem;
    font-size: 1rem;
    text-align: left;
    visibility: hidden;
    z-index: 10;
}

/* ----  ANIMATED CLASSES ---- */

.animated3MoveLeftAndScale{
    -webkit-animation: move-left-and-scale-animation .6s forwards ease-in-out;
            animation: move-left-and-scale-animation .6s forwards ease-in-out;
}

.animated4CloudUp{
    top: 35vh;
    transition: top .5s cubic-bezier(0,0,0,1);
}

.animated5VideoUp{
    top: 10;
    -webkit-transform: translateX(-50%) translateY(-62%);
            transform: translateX(-50%) translateY(-62%);
    transition: all .5s cubic-bezier(0,0,0,1);
}

.animated6TextUp{
    -webkit-animation: moveInBottom .4s ease-out;
            animation: moveInBottom .4s ease-out;
    visibility: visible;
}

.animated7ContainerUp{
    overflow: visible;
    -webkit-animation: image-movement2 .5s forwards ease-in-out;
            animation: image-movement2 .5s forwards ease-in-out;
}
.animated8BackgroundColorChange{
    background-color: white;
}

.animated9ImageAnimation{
    opacity: 1;
    -webkit-transform: translateX(-1.5rem) scale(1);
            transform: translateX(-1.5rem) scale(1);
    visibility: visible;
    transition: all .5s cubic-bezier(0,0,0,1) .2s;
}

/* ---- MEDIA QUERIES ---- */

@media only screen and (max-width: 75em){
    .animated4CloudUp{
        top: 41vh;
    }
}



/* ---- ANIMATIONS ---- */

@-webkit-keyframes move-left-and-scale-animation{
    50%{
        -webkit-transform: translateX(-25%);
                transform: translateX(-25%);
    }
    100%{
        -webkit-transform: translateX(-25%) scale(1.6);
                transform: translateX(-25%) scale(1.6);
        background-color: white;
    }
}

@keyframes move-left-and-scale-animation{
    50%{
        -webkit-transform: translateX(-25%);
                transform: translateX(-25%);
    }
    100%{
        -webkit-transform: translateX(-25%) scale(1.6);
                transform: translateX(-25%) scale(1.6);
        background-color: white;
    }
}

@-webkit-keyframes image-movement2{
    0%{
        -webkit-transform: translateX(-25%) scale(1.6);
                transform: translateX(-25%) scale(1.6);
        background-color: white;
    }
    100%{
        -webkit-transform: translateX(-25%) scale(1.6) translateY(-25%);
                transform: translateX(-25%) scale(1.6) translateY(-25%);
        background-color: white;
    }
}

@keyframes image-movement2{
    0%{
        -webkit-transform: translateX(-25%) scale(1.6);
                transform: translateX(-25%) scale(1.6);
        background-color: white;
    }
    100%{
        -webkit-transform: translateX(-25%) scale(1.6) translateY(-25%);
                transform: translateX(-25%) scale(1.6) translateY(-25%);
        background-color: white;
    }
}

@-webkit-keyframes moveInBottom {
    0%{
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInBottom {
    0%{
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}



/* MEDIA QUERIES FOR GRID */

@media only screen and (max-width: 950px){
    .main-video__grid{
        
        margin-top: 400px;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, 1fr);
        grid-row-gap: 5rem;
    }

    .main-video__grid-shape{
        justify-self: center;
        overflow: hidden;
        width: 16.5rem;
        height: 25rem;
    }

}

@media only screen and (max-width: 740px){
    .main-video__grid{
        width: 130%;
        margin-top: 300px;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
    .main-video__grid-shape{
        width: 0;
        height: 0;
    }
    .main-video__grid-image{
        height: 0;
        -webkit-transform: translateX(-1.5rem) scale(2);
                transform: translateX(-1.5rem) scale(2);
    }
    .main-video__grid-text{
        padding-left: 0rem;
        padding-right: 0rem;
        font-size: 1rem;
        text-align: left;
        z-index: 10;
    }
}

@media only screen and (max-width: 470px){
    .main-video__grid{
        width: 150%;
    }
    .main-video__cloudsimg{
        position: absolute;
        width: 100%;
        top: 100%;
    }
}
